	isTronLink() {
		//  && ; (1,tronWeb)
		let promise = new Promise(function(resolve, reject) {
			setTimeout(() => {
				if (window.tronWeb) {
					// false, , ;
					if (window.tronWeb.defaultAddress.base58) {
						resolve({
							status: true,
							data: ""
						});
					} else {
						resolve({
							status: false,
							data: ""
						});
					}
				} else {
					resolve({
						status: false,
						data: ""
					});
				}
			}, 1000);
		});
		// 
		return promise;
	},

