	async formatSeconds(value) {
		var secondTime = parseInt(value);// 
		var minuteTime = 0;// 
		var hourTime = 0;// 
		if(secondTime > 60) {//60
			//60
			minuteTime = parseInt(secondTime / 60);
			//
			secondTime = parseInt(secondTime % 60);
			//60
			if(minuteTime > 60) {
				//60
				hourTime = parseInt(minuteTime / 60);
				//60
				minuteTime = parseInt(minuteTime % 60);
			}
		}
		var result = "" + parseInt(secondTime) + ":";

		if(minuteTime > 0) {
			result = "" + parseInt(minuteTime) + ":" + result;
		}
		if(hourTime > 0) {
			result = "" + parseInt(hourTime) + ":" + result;
		}
		return result;
	},
	//
	async userData(_addr) {
		let res = await this.gMasterContract.accounts(_addr).call();
		let nodeLevel = res.nodeLevel._hex;////====> "V1/V2/V3/V4",1=>V1,2=>V2,3=>V3,4=>V4
		let joinTime = res.joinTime._hex;
		let nowTimeSec=Date.parse(new Date());
		let sec100Days = 86400*100;
		let countdown100DaySec = (parseInt(joinTime)+sec100Days)-nowTimeSec;
		let cd100DayString = await this.formatSeconds(countdown100DaySec);//====> ""
		let dayPoolIncome = res.dayPoolIncome._hex;//====> ""
		let weekPoolIncome = res.weekPoolIncome._hex;//====> ""
		let dynamicIncome = res.dynamicIncome._hex;//====> ""
		let reward = res.reward._hex;//====> ""
		let oneDaySec = 86400;
		let countdownTime  = oneDaySec - (nowTimeSec-joinTime)%oneDaySec;
		let cdTimeString = await this.formatSeconds(countdownTime);//====> ""
		let deposit = res.deposit._hex;//====> ""
		let maxIncome = res.maxIncome._hex;
		let AcceleratedExit = parseInt(maxIncome)/parseInt(deposit);//====> ""
		let untakeIncome = res.income._hex;//====> ""
		return res;
	},
